body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-image: radial-gradient(circle, #2C3A47, #000428);
    color: #fff;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
}

header {
    text-align: center;
    padding: 20px 0;
    font-size: 2.5rem;
}

header h1 {
    font-size: 2em;
    margin: 0;
    color: rgb(244, 97, 97);
}

header nav {
    margin-top: 10px;
}

header nav a {
    color: #fff;
    text-decoration: none;
    margin: 0 5px;
}

section.about-us {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

section.about-us .content {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
}

section.about-us .content img {
    max-width: 100%;
    height: auto;
    margin-right: 20px;
}

section.about-us .content .text {
    max-width: 500px;
}

section.about-us .content .text h2 {
    font-size: 1.5em;
    margin: 0 0 10px 0;
}

section.about-us .content .text p {
    font-size: 1em;
    margin: 0 0 20px 0;
}

section.about-us .content .text button {
    padding: 10px 20px;
    font-size: 1em;
    color: #000;
    background-color: #fff;
    border: none;
    cursor: pointer;
}

section.about-us .skills {
    max-width: 600px;
    width: 100%;
    text-align: left;
    margin-bottom: 30px;
}

section.about-us .skills h2 {
    font-size: 1.5em;
    margin: 0 0 20px 0;
}

section.about-us .skills .skill {
    margin-bottom: 20px;
}

section.about-us .skills .skill p {
    margin: 0 0 5px 0;
}

section.about-us .skills .progress {
    width: 100%;
    background-color: #444;
    border-radius: 5px;
    overflow: hidden;
}

section.about-us .skills .progress .progress-bar {
    height: 10px;
    background-color: #fff;
}

section.about-us .stats {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 800px;
}

section.about-us .stats .stat {
    text-align: center;
}

section.about-us .stats .stat h3 {
    font-size: 2em;
    margin: 0;
}

section.about-us .stats .stat p {
    margin: 5px 0 0 0;
}
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.AboutText {
    animation-timeline: view();
    animation-range : entry 0% cover 50%;
    opacity: 0;
    animation: fadeIn 1s ease-in forwards;
   
}
