.loginPage{
height: 150vh;
width: 100vw;
border: 1px solid black;
position: absolute;
background-color: rgb(255, 255, 255);
opacity: 1;
z-index: 2;
}
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f3f3f3;
}

.loginPage-container {
    display: flex;
    height: 100vh;
}

.loginPage-left, .loginPage-right {
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.loginPage-left {
    background-color: #fff;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 10%;
}

.loginPage-left h2 {
    font-size: 2em;
    margin-bottom: 10px;
}

.loginPage-left p {
    color: #6b6b6b;
    margin-bottom: 20px;
}

.loginPage-left a {
    color: #7b1fa2;
    text-decoration: none;
}

.loginPage-inputGroup {
    margin-bottom: 15px;
    width: 100%;
}

.loginPage-inputGroup input {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.loginPage-rememberForgot {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin-bottom: 20px;
}

.loginPage-loginButton {
    background-color: #7b1fa2;
    color: white;
    padding: 10px 20px;
    font-size: 1em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.loginPage-socialLogin {
    margin-top: 20px;
    text-align: center;
    width: 100%;
}

.loginPage-socialLogin p {
    margin-bottom: 10px;
}

.loginPage-socialLogin button {
    background-color: #3b5998;
    color: white;
    padding: 10px 20px;
    font-size: 1em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
}

.loginPage-twitter {
    background-color: #1da1f2;
}

.loginPage-right {
    background: linear-gradient(to bottom right, #6a1b9a, #7b1fa2);
    color: white;
    text-align: center;
    padding-right: 10%;
}

.loginPage-right h2 {
    font-size: 3em;
    margin-bottom: 20px;
}

.loginPage-right p {
    font-size: 1.2em;
    line-height: 1.5;
}
