
.cart-container {
    display: flex;
    width: 100%;
}

.cart-sidebar {
    width: 250px;
    background-image: radial-gradient(circle, #2d79ba, #040723);
    color: #fff;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.cart-logo {
    font-size: 24px;
    margin-bottom: 20px;
}

.cart-logo span {
    font-weight: normal;
}

.cart-nav {
    display: flex;
    flex-direction: column;
}

.cart-nav a {
    color: #fff;
    text-decoration: none;
    margin-bottom: 15px;
    padding: 10px;
    border-radius: 5px;
}

.cart-nav a.cart-active,
.cart-nav a:hover {
    background-color: #ff4081;
}

.cart-main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 20px;
}

.cart-header {
    margin-bottom: 20px;
}

.cart-header h1 {
    margin: 0;
    font-size: 24px;
}

.cart-items {
    flex: 1;
}

.cart-item {
    display: flex;
    align-items: center;
    background-image: radial-gradient(circle, #121a21, #040723);
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 15px;
}

.cart-item img {
    width: 60px;
    height: 60px;
    margin-right: 15px;
}

.cart-item-info {
    flex: 1;
}

.cart-item-info h3 {
    margin: 0 0 10px 0;
}

.cart-quantity {
    display: flex;
    align-items: center;
}

.cart-quantity button {
    background-color: #ff4081;
    color: #fff;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
}

.cart-quantity input {
    width: 40px;
    text-align: center;
    border: 1px solid #ddd;
    margin: 0 10px;
    padding: 5px;
    border-radius: 5px;
}

.cart-price {
    font-size: 18px;
    font-weight: bold;
}

.cart-summary {
    width: 300px;
    background-image: radial-gradient(circle, #121a21, #040723);
    padding: 20px;
    border-radius: 5px;
}

.cart-summary h2 {
    margin: 0 0 20px 0;
    font-size: 22px;
}

.cart-summary-item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}

.cart-summary-item span {
    font-size: 18px;
}

.cart-checkout {
    background-color: #ff4081;
    color: #fff;
    border: none;
    padding: 10px;
    width: 100%;
    border-radius: 5px;
    cursor: pointer;
    font-size: 18px;
}

.cart-buds {
    height: 50px;
    width: 50px;
    margin-right: 5px;
    background-image: url("./buds\ upscaled.png");
    background-size: 210% 140%;
    background-position-x:-25px ;
    background-position-y:-10px ;
}

.cart-wireless{
    height: 50px;
    width: 50px;
    margin-right: 5px;
    background-image: url("./wireless\ headset.png");
    background-size: 150% 140%;
    background-position-x:-15px ;
    background-position-y:-10px ;
}

.cart-wired{
    height: 50px;
    width: 50px;
    margin-right: 5px;
    background-image: url("./wired_headphones\ main.png");
    background-size: 100% 100%;
}

.cart-adds{
    border: 3px solid rgb(255, 255, 255);
    height: 500px;
    width: 250px;
    background-image: url("./add.jpg");
    background-size: 100% 100%;
}