
.zippy-zorp-container {
 
    background-image: radial-gradient(circle, #2C3A47, #000428);
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: -webkit-fill-available;
}

.flibber-header, .flobber-footer {
    text-align: center;
    margin-bottom: 20px;
}

.whizzle-title {
    font-size: 2.5em;
    margin: 0;
}

.froodle-subtitle {
    font-size: 1.5em;
    margin: 0;
    color: #b0b0b0;
}

.yibber-form {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding: 50px 300px;
}

.splunk-row {
    margin-bottom: 15px;
}

.splurk-label {
    display: block;
    margin-bottom: 5px;
    color: #b6b6b6;
}

.glorp-services {
    display: flex;
    justify-content: space-between;
}

.glorp-services button {
    flex: 1;
    margin-right: 5px;
    padding: 10px;
    border: none;
    background-color: #959595;
    color: #000000;
    border-radius: 5px;
    cursor: pointer;
}

.glorp-services button:last-child {
    margin-right: 0;
}

.plip-input, .plop-textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #444;
    border-radius: 5px;
    background-color: #8c8c8c;
    color: #010101;
    font-weight: 500;
}

.plop-textarea {
    height: 100px;
    resize: none;
}

.floop-submit {
    padding: 10px;
    border: none;
    background-color: #f36;
    color: #f0f0f0;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1em;
    margin-top: 10px;
}

.floop-submit:hover {
    background-color: #d22;
}

.yab-text {
    color: #b0b0b0;
    font-size: 0.9em;
}

.fizz-icons {
    display: flex;
    justify-content: center;
    gap: 15px;
    margin-top: 10px;
}

.fizz-icons a {
    color: #f0f0f0;
    text-decoration: none;
}

.fizz-icons a:hover {
    text-decoration: underline;
}
