@keyframes appear {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.main1{
color: white;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;

}

.gradient-background {
    background-image: radial-gradient(circle, #2C3A47 , #000428)
}
.main1-2{

    color: white;
    margin-right: 10px;
    width: 600px;
    
}

.Image{
    height: 500px;
    width: 700px;
    background-image: url(./buds\ upscaled.png);
    background-size: 150% 150%;
    background-position-x:65px;
    background-position-y:-60px ;
    
    animation-timeline: view();
    animation: appear 1s ease-in forwards;
    opacity: 0;
    animation-range: entry 0% cover 50%;
}

.section1-button{
    border: 2px solid white ;
    border-radius: 20px;
    width: 100px;
    color: white;
    text-align: center;
    padding: 10px;
    background-color: black;
}
.main1-section2{
    text-align: center;
    color: white;
    padding-bottom: 30px;
}

p{
    margin-bottom: 0px;
}