.header {
    display: flex;
    justify-content: space-between;
    padding: 0px 10px ;
    color: rgb(255, 255, 255);
    line-height: 70px;
    position: sticky;
    background-image: radial-gradient(circle, #040f1a , #000428) !important;

}
.logo {
    height: 40px;
    width: 40px;
}

a {
    color: white;
    text-decoration: none;
    cursor: pointer;
}
a:hover {
    font-size: 1.2em;
    border-radius: 25px;
    padding: -5px 5px;
}

.left {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    width: 120px;
}



.right {
    width: 400px;
    display: flex;
    justify-content: space-between;
}
