
footer {
    background-image: radial-gradient(circle, #040f1a , #000428);
    color: #fff;
    padding: 20px 0;
    padding-top: 30px;
}

.footer-container {
    display: flex;
    justify-content: space-around;
    padding: 20px;
}

.footer-section {
    width: 30%;
}

.footer-section h2 {
    font-size: 16px;
    margin-bottom: 10px;
}

.footer-section ul {
    list-style: none;
    padding: 0;
}

.footer-section ul li {
    margin-bottom: 10px;
}

.footer-section ul li a {
    color: #fff;
    text-decoration: none;
}

.footer-section ul li a:hover {
    text-decoration: underline;
}

.newsletter input[type="email"] {
    padding: 10px;
    width: 70%;
    border: none;
    border-radius: 4px;
}

.newsletter button {
    padding: 10px 20px;
    border: none;
    background-color: #e60000;
    color: #fff;
    border-radius: 4px;
    cursor: pointer;
}

.newsletter button:hover {
    background-color: #cc0000;
}

.footer-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border-top: 1px solid #444;
}

.footer-logo img {
    width: 100px;
}

.footer-social a {
    margin: 0 10px;
    display: inline-block;
}

.footer-social img {
    width: 24px;
}

.footer-legal {
    text-align: center;
    padding: 20px;
    border-top: 1px solid #444;
}

.footer-legal ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.footer-legal ul li {
    display: inline;
    margin: 0 10px;
}

.footer-legal ul li a {
    color: #fff;
    text-decoration: none;
}

.footer-legal ul li a:hover {
    text-decoration: underline;
}
