
.container {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    text-align: center;
}

@keyframes headset-wired {
    from{
        opacity: 0;
    }
    to{
        opacity :1;
    }
}

.wiredImage {
    width: 400px;
    height:600px;
    margin-bottom: 20px;
    background-image: url('./wired_headphones\ main.png');
    background-size: cover;
    background-repeat: no-repeat;
    
    animation-timeline: view();
    animation: fadeIn 1s ease-in forwards;
    opacity: 0;
    animation-range : entry 0% cover 50%;
}

.branding {
    width: 700px;
    
}

.logo {
    font-size: 24px;
    font-weight: bold;
    color: #ff2e2e;
    margin-bottom: 20px;
}

.title h1 {
    font-size: 48px;
    margin: 0;
}

.title .and {
    color: gray;
}

.description p {
    font-size: 16px;
    color: #b3b3b3;
    margin-top: 20px;
}
