

.headerR{
    
    display:flex;
    justify-content: space-evenly;
    height: 100vh;
    align-items: center;
}

  .buy-now-navV {
    background-color: #007bff;
    color: #ffffff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }

  .contentT {
    margin-top: 20px;
    
  }

  .contentT h1 {
    font-size: 48px;
    margin-bottom: 20px;
  }

  .contentT p {
    font-size: 18px;
    margin-bottom: 40px;
  }

  .buttonsS {
    display: flex;
    gap: 20px;
  }

  .buy-nowW, .exploreE {
    background-color: #007bff;
    color: #ffffff;
    border: none;
    padding: 15px 30px;
    border-radius: 5px;
    cursor: pointer;
  }

  .exploreE {
    background-color: transparent;
    border: 1px solid #007bff;
    color: #007bff;
  }

  .headphones-imageE {
    height: 600px;
    width: 600px;
    background-image: url("./wireless\ headset.png");
    background-size: 120% ;
    background-repeat: no-repeat;
    background-position-y: -280px ;
    background-position-x:-50px ;
  }
  @keyframes wireless {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
.wireless {
    animation-timeline: view();
    animation-range : entry 0% cover 50%;
    opacity: 0;
    animation: wireless 1s ease-in forwards;
   
}
